"use client";

import { useEffect, useMemo, useState } from "react";
import type { Wod } from "@/lib/mockWod";
import { deleteWod, loadWods, upsertWod } from "@/lib/wodStore";
import { Modal } from "@/components/ui/modal";
import { useModal } from "@/hooks/useModal";

const today = new Date().toISOString().slice(0, 10);

function toLines(items?: string[]) {
  return (items ?? []).join("\n");
}
function fromLines(value: string) {
  return value
    .split("\n")
    .map((s) => s.trim())
    .filter(Boolean);
}

const emptyWod = (date: string): Wod => ({
  date,
  title: "",
  intent: "",
  warmup: [],
  strength: [],
  metcon: [],
  rx: [],
  scaled: [],
});

type Flash = { type: "success" | "danger"; text: string } | null;

export default function AdminWodPage() {
  const [list, setList] = useState<Wod[]>([]);
  const [draft, setDraft] = useState<Wod>(() => emptyWod(today));
  const [flash, setFlash] = useState<Flash>(null);

  const { isOpen, openModal, closeModal } = useModal();

  const refresh = () => setList(loadWods());

  useEffect(() => {
    refresh();
  }, []);

  const selectedDate = draft.date;

  const hasExisting = useMemo(
    () => list.some((w) => w.date === selectedDate),
    [list, selectedDate]
  );

  const onSave = () => {
    upsertWod({
      ...draft,
      warmup: draft.warmup ?? [],
      strength: (draft.strength ?? []).filter(Boolean),
      metcon: draft.metcon ?? [],
      rx: draft.rx ?? [],
      scaled: draft.scaled ?? [],
    });

    refresh();
    setFlash({ type: "success", text: "WOD guardado ‚úÖ" });
    setTimeout(() => setFlash(null), 2000);
  };

  const onAskDelete = () => openModal();

  const onConfirmDelete = () => {
    deleteWod(selectedDate);
    refresh();
    setDraft(emptyWod(today));
    closeModal();
    setFlash({ type: "danger", text: "WOD borrado üóëÔ∏è" });
    setTimeout(() => setFlash(null), 2000);
  };

  return (
    <div className="max-w-5xl">
      <div className="flex items-start justify-between gap-4">
        <div>
          <h1 className="text-xl font-semibold">WOD</h1>
          <p className="mt-1 text-sm text-gray-500">
            Crear, editar y borrar entrenamientos.
          </p>
        </div>

        <button
          onClick={() => setDraft(emptyWod(today))}
          className="rounded-xl border border-gray-300 bg-white px-4 py-2 text-sm"
        >
          Nuevo
        </button>
      </div>

      {flash && (
        <div
          className={`mt-4 rounded-xl border px-4 py-3 text-sm ${
            flash.type === "success"
              ? "border-green-200 bg-green-50 text-green-800"
              : "border-red-200 bg-red-50 text-red-800"
          }`}
        >
          {flash.text}
        </div>
      )}

      <div className="mt-4 grid gap-4 lg:grid-cols-3">
        {/* LISTA */}
        <div className="rounded-2xl border border-gray-200 bg-white p-4 shadow-sm">
          <div className="text-sm font-semibold">Publicados</div>

          <div className="mt-3 space-y-2">
            {list.length === 0 && (
              <div className="text-sm text-gray-500">Todav√≠a no hay WODs.</div>
            )}

            {list.map((w) => (
              <button
                key={w.date}
                onClick={() => setDraft(w)}
                className="w-full rounded-xl border border-gray-200 p-3 text-left hover:bg-gray-50"
              >
                <div className="text-xs text-gray-500">{w.date}</div>
                <div className="mt-1 text-sm font-medium">
                  {w.title || "Sin t√≠tulo"}
                </div>
              </button>
            ))}
          </div>
        </div>

        {/* FORM */}
        <div className="lg:col-span-2 rounded-2xl border border-gray-200 bg-white p-4 shadow-sm">
          <div className="flex items-center justify-between gap-3">
            <div className="text-sm font-semibold">
              {hasExisting ? "Editar WOD" : "Nuevo WOD"}
            </div>

            <div className="flex gap-2">
              {hasExisting && (
                <button
                  onClick={onAskDelete}
                  className="rounded-xl border border-red-300 bg-white px-4 py-2 text-sm text-red-700"
                >
                  Borrar
                </button>
              )}
              <button
                onClick={onSave}
                className="rounded-xl bg-gray-900 px-4 py-2 text-sm text-white"
              >
                Guardar
              </button>
            </div>
          </div>

          <div className="mt-4 grid gap-3">
            <label className="text-sm">
              <div className="mb-1 text-gray-500">Fecha</div>
              <input
                type="date"
                value={draft.date}
                onChange={(e) => setDraft({ ...draft, date: e.target.value })}
                className="w-full rounded-lg border px-3 py-2"
              />
            </label>

            <label className="text-sm">
              <div className="mb-1 text-gray-500">T√≠tulo</div>
              <input
                value={draft.title}
                onChange={(e) => setDraft({ ...draft, title: e.target.value })}
                className="w-full rounded-lg border px-3 py-2"
              />
            </label>

            <label className="text-sm">
              <div className="mb-1 text-gray-500">Intenci√≥n</div>
              <textarea
                value={draft.intent}
                onChange={(e) => setDraft({ ...draft, intent: e.target.value })}
                className="w-full rounded-lg border px-3 py-2"
              />
            </label>

            <label className="text-sm">
              <div className="mb-1 text-gray-500">
                Warm-up (1 l√≠nea por item)
              </div>
              <textarea
                value={toLines(draft.warmup)}
                onChange={(e) =>
                  setDraft({ ...draft, warmup: fromLines(e.target.value) })
                }
                className="w-full rounded-lg border px-3 py-2"
              />
            </label>

            <label className="text-sm">
              <div className="mb-1 text-gray-500">
                Strength (1 l√≠nea por item)
              </div>
              <textarea
                value={toLines(draft.strength)}
                onChange={(e) =>
                  setDraft({ ...draft, strength: fromLines(e.target.value) })
                }
                className="w-full rounded-lg border px-3 py-2"
              />
            </label>

            <label className="text-sm">
              <div className="mb-1 text-gray-500">
                Metcon (1 l√≠nea por item)
              </div>
              <textarea
                value={toLines(draft.metcon)}
                onChange={(e) =>
                  setDraft({ ...draft, metcon: fromLines(e.target.value) })
                }
                className="w-full rounded-lg border px-3 py-2"
              />
            </label>

            <div className="grid gap-3 md:grid-cols-2">
              <label className="text-sm">
                <div className="mb-1 text-gray-500">RX (1 l√≠nea por item)</div>
                <textarea
                  value={toLines(draft.rx)}
                  onChange={(e) =>
                    setDraft({ ...draft, rx: fromLines(e.target.value) })
                  }
                  className="w-full rounded-lg border px-3 py-2"
                />
              </label>

              <label className="text-sm">
                <div className="mb-1 text-gray-500">
                  Scaled (1 l√≠nea por item)
                </div>
                <textarea
                  value={toLines(draft.scaled)}
                  onChange={(e) =>
                    setDraft({ ...draft, scaled: fromLines(e.target.value) })
                  }
                  className="w-full rounded-lg border px-3 py-2"
                />
              </label>
            </div>
          </div>
        </div>
      </div>

      {/* CONFIRM MODAL */}
      <Modal
  isOpen={isOpen}
  onClose={closeModal}
  className="max-w-[420px] p-5 lg:p-6"
>
  <div className="text-center">
    <div className="relative flex items-center justify-center z-1 mb-5">
      <svg
        className="fill-error-50 dark:fill-error-500/15"
        width="72"
        height="72"
        viewBox="0 0 90 90"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          d="M34.364 6.85053C38.6205 -2.28351 51.3795 -2.28351 55.636 6.85053C58.0129 11.951 63.5594 14.6722 68.9556 13.3853C78.6192 11.0807 86.5743 21.2433 82.2185 30.3287C79.7862 35.402 81.1561 41.5165 85.5082 45.0122C93.3019 51.2725 90.4628 63.9451 80.7747 66.1403C75.3648 67.3661 71.5265 72.2695 71.5572 77.9156C71.6123 88.0265 60.1169 93.6664 52.3918 87.3184C48.0781 83.7737 41.9219 83.7737 37.6082 87.3184C29.8831 93.6664 18.3877 88.0266 18.4428 77.9156C18.4735 72.2695 14.6352 67.3661 9.22531 66.1403C-0.462787 63.9451 -3.30193 51.2725 4.49185 45.0122C8.84391 41.5165 10.2138 35.402 7.78151 30.3287C3.42572 21.2433 11.3808 11.0807 21.0444 13.3853C26.4406 14.6722 31.9871 11.951 34.364 6.85053Z"
          fill=""
          fillOpacity=""
        />
      </svg>

      <span className="absolute -translate-x-1/2 -translate-y-1/2 left-1/2 top-1/2">
        <svg
          className="fill-error-600 dark:fill-error-500"
          width="32"
          height="32"
          viewBox="0 0 38 38"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            fillRule="evenodd"
            clipRule="evenodd"
            d="M9.62684 11.7496C9.04105 11.1638 9.04105 10.2141 9.62684 9.6283C10.2126 9.04252 11.1624 9.04252 11.7482 9.6283L18.9985 16.8786L26.2485 9.62851C26.8343 9.04273 27.7841 9.04273 28.3699 9.62851C28.9556 10.2143 28.9556 11.164 28.3699 11.7498L21.1198 18.9999L28.3699 26.25C28.9556 26.8358 28.9556 27.7855 28.3699 28.3713C27.7841 28.9571 26.8343 28.9571 26.2485 28.3713L18.9985 21.1212L11.7482 28.3715C11.1624 28.9573 10.2126 28.9573 9.62684 28.3715C9.04105 27.7857 9.04105 26.836 9.62684 26.2502L16.8771 18.9999L9.62684 11.7496Z"
            fill=""
          />
        </svg>
      </span>
    </div>

    <h4 className="mb-2 text-xl font-semibold text-gray-800 dark:text-white/90">
      Borrar WOD
    </h4>

    <p className="text-sm leading-6 text-gray-500 dark:text-gray-400">
      ¬øSeguro que quer√©s borrar el WOD del <b>{selectedDate}</b>?<br />
      Esta acci√≥n no se puede deshacer.
    </p>

    <div className="mt-6 flex items-center justify-center gap-3">
      <button
        type="button"
        onClick={closeModal}
        className="rounded-lg border border-gray-300 bg-white px-4 py-2 text-sm font-medium text-gray-700 hover:bg-gray-50"
      >
        Cancelar
      </button>

      <button
        type="button"
        onClick={onConfirmDelete}
        className="rounded-lg bg-error-500 px-4 py-2 text-sm font-medium text-white shadow-theme-xs hover:bg-error-600"
      >
        S√≠, borrar
      </button>
    </div>
  </div>
</Modal>


    </div>
  );
}
